body{
  background: #151515;
}

.back{
  position: fixed;
  width: 100%;
  height: 100%;
}

.back img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.transparent{
  content: '';
  width: 100%;
  height: 100%;
  position: fixed;
  background: linear-gradient(0deg, rgb(0,0,0) 0%, rgba(15,23,30,0.35057773109243695) 100%);
  z-index: 100;
}

.leftBar{
  position: fixed;
  width: 400px;
  height: 40vh;
  background: transparent;
  top: 50%;
  left: 3vw;
}

.link{
  color: #fff;
  list-style: none;
  padding: 10px;
  font-size: 19px;
  cursor: pointer;
}

.link:hover{
  color: #7e7f49;
  text-shadow: 0 0 10px #fff0, 0 0 20px #fff, 0 0 30px #7e7f490a, 0 0 40px #9d9e53f2, 0 0 50px #7e7f4940, 0 0 60px #7e7f4947, 0 0 70px #7e7f4900;
}

*{
  user-select: none;
}